@import "normalize.css";
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rajdhani";
  font-display: swap;
  src: local("Rajdhani Regular"), local("Rajdhani-Regular"), url("../fonts/rajdhani-regular.woff2") format("woff2"), url("../fonts/rajdhani-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Rajdhani";
  font-display: swap;
  src: local("Rajdhani Medium"), local("Rajdhani-Medium"), url("../fonts/rajdhani-medium.woff2") format("woff2"), url("../fonts/rajdhani-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Rajdhani";
  font-display: swap;
  src: local("Rajdhani Semibold"), local("Rajdhani-Semibold"), url("../fonts/rajdhani-semibold.woff2") format("woff2"), url("../fonts/rajdhani-semibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Rajdhani";
  font-display: swap;
  src: local("Rajdhani Bold"), local("Rajdhani-Bold"), url("../fonts/rajdhani-bold.woff2") format("woff2"), url("../fonts/rajdhani-bold.woff") format("woff");
}
:root {
  --color-white: #ffffff;
  --color-black: #000000;
  --color-gray: #8a94a6;
  --color-gray-light: #e1e6ed;
  --color-gray-transparent: rgba(225, 230, 237, 0.2);
  --color-violet: #192b3f;
  --color-sandy: #c9b8a8;
  --color-blue: #3d5876;
  --color-primary: var(--color-violet);
  --color-primary-hover: #2e4055;
  --color-primary-active: #3b536d;
  --color-primary-transparent: rgba(25, 43, 63, 0.8);
}

html {
  min-height: 100vh;
  scroll-behavior: smooth;
  font: 500 16px/1.5 "Rajdhani", "Arial", sans-serif;
}

body {
  min-width: 320px;
  margin: 0;
  color: var(--color-primary);
  text-size-adjust: 100%;
  background-color: var(--color-white);
  -webkit-tap-highlight-color: transparent;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input[type=number] {
  appearance: textfield;
}
input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button {
  margin: 0;
  appearance: none;
}
input[type=search] {
  appearance: textfield;
}
input[type=search]::-webkit-search-decoration, input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-results-button, input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  box-sizing: border-box;
  min-width: 320px;
  padding-right: 16px;
  padding-left: 16px;
}
@media only screen and (min-width: 1360px) {
  .container {
    padding-right: 52px;
    padding-left: 52px;
  }
}

.button {
  display: block;
  width: 100%;
  min-height: 56px;
  padding: 0 12px;
  font: inherit;
  font-size: 24px;
  line-height: 32px;
  text-align: center;
  color: var(--color-white);
  background-color: var(--color-primary);
  border: none;
  cursor: pointer;
  transition: background-color 0.4s;
  appearance: none;
}
.button:hover, .button:focus {
  background-color: var(--color-primary-hover);
}
.button:focus {
  outline: none;
}
.button:active {
  background-color: var(--color-primary-active);
}

.slider {
  height: 600px;
  background-color: var(--color-sandy);
}
@media only screen and (min-width: 1360px) {
  .slider {
    height: 100vh;
    max-height: 768px;
  }
}

.slider__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.slider__header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2;
  padding: 24px 16px;
}
@media only screen and (min-width: 1360px) {
  .slider__header {
    padding: 32px 52px;
  }
}

.slider__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  width: 100%;
  height: 100%;
}
.no-js .slider__list {
  display: flex;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
}

.slider__slide {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  box-sizing: border-box;
  padding: 92px 16px;
  opacity: 0;
  transition: opacity 1s;
}
.slider__slide--active {
  z-index: 1;
  opacity: 1;
}
.no-js .slider__slide {
  position: relative;
  flex-shrink: 0;
  width: 100%;
  scroll-snap-align: start;
}
@media only screen and (min-width: 1360px) {
  .slider__slide {
    padding: 125px 40px 92px 52px;
  }
}

.slider__slide-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 600px;
  max-height: initial;
  object-position: top;
  object-fit: cover;
}
@media only screen and (min-width: 1360px) {
  .slider__slide-image {
    height: 768px;
  }
}

.slider__slide-content {
  position: relative;
  color: var(--color-white);
}

.slider__slide-title {
  margin: 0;
  font-weight: 400;
  font-size: 48px;
  line-height: 1;
}
@media only screen and (min-width: 720px) {
  .slider__slide-title {
    font-size: 64px;
  }
}
@media only screen and (min-width: 1360px) {
  .slider__slide-title {
    font-size: 72px;
  }
}

.slider__slide-text {
  margin: 0;
  opacity: 0.8;
}
@media only screen and (min-width: 720px) {
  .slider__slide-text {
    font-size: 24px;
    line-height: 32px;
  }
}
@media only screen and (min-width: 1360px) {
  .slider__slide-text {
    font-size: 28px;
  }
}

.slider__control {
  position: absolute;
  bottom: 27px;
  left: 16px;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  width: 230px;
  color: var(--color-white);
}
.no-js .slider__control {
  display: none;
}
@media only screen and (min-width: 1360px) {
  .slider__control {
    bottom: 32px;
    left: 52px;
  }
}

.slider__buttons {
  display: flex;
  justify-content: space-between;
  width: 80px;
}

.slider__button {
  box-sizing: border-box;
  width: 32px;
  height: 32px;
  padding: 0;
  color: inherit;
  background: none;
  border: 1px solid transparent;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.4s, border-color 0.4s;
  user-select: none;
  appearance: none;
}
.slider__button svg {
  transform: translate(-1px, -1px);
  transition: fill 0.4s;
  fill: currentColor;
}
.slider__button--next svg {
  transform: translate(-1px, -1px) rotate(180deg);
}
.slider__button:hover, .slider__button:focus {
  border-color: currentColor;
}
.slider__button:focus {
  outline: none;
}
.slider__button:active {
  background-color: currentColor;
}
.slider__button:active svg {
  fill: var(--color-gray);
}

.slider__pagination {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 70px;
  font-size: 20px;
  line-height: 1.6;
}
.slider__pagination::before {
  content: "";
  position: absolute;
  top: 16px;
  left: 50%;
  width: 16px;
  height: 1px;
  background-color: currentColor;
  transform: translateX(-50%);
}

.slider__pagination-total {
  opacity: 0.5;
}

.logo {
  display: inline-block;
}
.logo img {
  display: inline-block;
  width: 65px;
  height: 36px;
}
@media only screen and (min-width: 1360px) {
  .logo img {
    width: 94px;
    height: 53px;
  }
}

.product {
  margin-bottom: 40px;
  padding-top: 8px;
}
@media only screen and (min-width: 1360px) {
  .product {
    padding-top: 56px;
  }
}

.product__heading {
  margin-bottom: 16px;
}

.product__subtitle {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  color: var(--color-gray);
  text-transform: uppercase;
  margin-bottom: 16px;
}
@media only screen and (min-width: 1360px) {
  .product__subtitle {
    margin-bottom: 24px;
  }
}

.product__title {
  margin: 0;
  font-weight: 500;
  font-size: 32px;
  line-height: 1;
}
@media only screen and (min-width: 720px) {
  .product__title {
    font-size: 48px;
    line-height: 56px;
  }
}

.product__description {
  margin-bottom: 16px;
  font-size: 18px;
  line-height: 28px;
  color: var(--color-gray);
  opacity: 0.8;
}
.product__description p {
  margin: 0;
}
.product__description p:not(:last-child) {
  margin-bottom: 16px;
}
@media only screen and (min-width: 720px) {
  .product__description {
    margin-bottom: 4px;
    font-size: 20px;
    line-height: 1.6;
  }
}
@media only screen and (min-width: 1360px) {
  .product__description {
    width: 385px;
    margin-bottom: 8px;
  }
}

.product__form {
  margin-bottom: 28px;
  border: solid var(--color-gray-light);
  border-width: 1px 0;
}
@media only screen and (min-width: 720px) {
  .product__form {
    margin-bottom: 18px;
  }
}
@media only screen and (min-width: 1360px) {
  .product__form {
    margin-bottom: 24px;
  }
}

.product__details {
  margin-bottom: 40px;
}
.product__details p {
  margin: 0;
}
.product__details p:not(:last-child) {
  margin-bottom: 8px;
}
@media only screen and (min-width: 1360px) {
  .product__details {
    margin-bottom: 72px;
  }
}

.product__details-title {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  color: var(--color-gray);
  text-transform: uppercase;
  margin-bottom: 8px;
}

.product__composition {
  line-height: 1.25;
  color: var(--color-gray);
}

.product__button {
  margin-bottom: 40px;
}
@media only screen and (min-width: 1360px) {
  .product__button {
    margin-bottom: 32px;
  }
}

.product__delivery {
  margin: 0;
}
.product__delivery dd {
  display: none;
  margin: 0;
  color: var(--color-gray);
}
.product__delivery dd p {
  margin: 0;
  margin-bottom: 16px;
}
.product__delivery dd b {
  font-weight: 600;
}

.product__delivery-term {
  border-bottom: 1px solid var(--color-gray-light);
}
.product__delivery-term--opened dd {
  display: block;
}
.no-js .product__delivery-term dd {
  display: block;
}
.product__delivery-term:not(:last-child) {
  margin-bottom: 16px;
}

.product__delivery-button {
  display: block;
  width: 100%;
  margin-bottom: 8px;
  padding: 0;
  font: inherit;
  font-size: 24px;
  line-height: 32px;
  text-align: left;
  color: inherit;
  background: url("../img/plus.svg") right/18px no-repeat;
  border: none;
  cursor: pointer;
  user-select: none;
  appearance: none;
}
.product__delivery-term--opened .product__delivery-button {
  margin-bottom: 16px;
  background-image: url("../img/minus.svg");
}
.no-js .product__delivery-button {
  background: none;
  cursor: default;
}

.order-form {
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: 24px 68px;
  padding-top: 16px;
  padding-bottom: 16px;
}
@media only screen and (min-width: 720px) {
  .order-form {
    row-gap: 36px;
    padding-top: 24px;
    padding-bottom: 14px;
  }
}
@media only screen and (min-width: 1360px) {
  .order-form {
    gap: 32px 52px;
    padding-top: 16px;
    padding-bottom: 16px;
  }
}

.order-form__fieldset {
  margin: 0;
  padding: 0;
  border: none;
}
@media only screen and (max-width: 719px) {
  .order-form__fieldset:nth-child(-n+2) {
    grid-column: 1/-1;
  }
}

.order-form__title {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  color: var(--color-gray);
  text-transform: uppercase;
  padding: 0;
}

.order-form__colors {
  margin-top: 8px;
}

@media only screen and (min-width: 1360px) {
  .order-form__sizes {
    margin-top: 8px;
  }
}

.order-form__quantity {
  margin-top: 12px;
}

.order-form__cost {
  margin: 0;
  margin-top: 8px;
  font-size: 40px;
  line-height: 40px;
  color: var(--color-sandy);
}

.order-form__cost-fraction {
  font-size: 28px;
  color: var(--color-gray);
}

.colors {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.colors__color {
  position: relative;
  width: 48px;
  height: 48px;
}
.colors__color input {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  appearance: none;
}
.colors__color input:checked + label {
  border-color: var(--color-primary);
}
.colors__color input:hover + label, .colors__color input:focus + label {
  border-color: var(--color-gray);
}
.colors__color label {
  display: block;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background: none;
  border: 1px solid var(--color-white);
  border-radius: 50%;
  box-shadow: inset 0 0 0 4px var(--color-white);
  transition: border-color 0.4s;
  user-select: none;
}
.colors__color--violet label {
  background-image: radial-gradient(var(--color-violet) 65%, transparent 65%);
}
.colors__color--gray label {
  background-image: radial-gradient(var(--color-gray) 65%, transparent 65%);
}
.colors__color--blue label {
  background-image: radial-gradient(var(--color-blue) 65%, transparent 65%);
}
@media only screen and (min-width: 1360px) {
  .colors__color {
    width: 32px;
    height: 32px;
  }
  .colors__color label {
    box-shadow: inset 0 0 0 3px var(--color-white);
  }
}

@media only screen and (min-width: 720px) {
  .sizes {
    display: flex;
  }
}

.sizes__list {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  margin-bottom: 24px;
}
@media only screen and (min-width: 720px) {
  .sizes__list {
    margin-right: 26px;
    margin-bottom: 0;
  }
}
@media only screen and (min-width: 1360px) {
  .sizes__list {
    margin-right: 16px;
  }
}

.sizes__size {
  position: relative;
  width: 48px;
  height: 48px;
}
.sizes__size input {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  appearance: none;
}
.sizes__size input:checked + label {
  font-size: 28px;
  color: var(--color-primary);
  border-color: currentColor;
}
.sizes__size input:hover + label, .sizes__size input:focus + label {
  border-color: var(--color-gray);
}
.sizes__size input:disabled {
  cursor: default;
}
.sizes__size input:disabled + label {
  color: var(--color-gray-light);
  border-color: transparent;
  opacity: 0.5;
}
.sizes__size label {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  font-size: 20px;
  line-height: 24px;
  color: var(--color-gray);
  border: 1px solid transparent;
  border-radius: 50%;
  transition: border-color 0.4s;
  user-select: none;
}
@media only screen and (min-width: 1360px) {
  .sizes__size {
    width: 32px;
    height: 32px;
  }
  .sizes__size input:checked + label {
    font-size: 20px;
  }
  .sizes__size label {
    font-size: 16px;
  }
}

.sizes__link {
  display: inline-block;
  box-sizing: border-box;
  padding: 1px 12px;
  vertical-align: middle;
  color: var(--color-sandy);
  border: 1px solid currentColor;
  border-radius: 24px;
}
@media only screen and (min-width: 720px) {
  .sizes__link {
    align-self: flex-end;
    margin-bottom: 12px;
  }
}
@media only screen and (min-width: 1360px) {
  .sizes__link {
    margin-bottom: 2px;
  }
}

.quantity {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 84px;
}
.no-js .quantity {
  justify-content: center;
}

.quantity__button {
  box-sizing: border-box;
  width: 28px;
  height: 28px;
  padding: 0;
  background: none;
  border: 1px solid transparent;
  border-radius: 50%;
  cursor: pointer;
  transition: border-color 0.4s;
  appearance: none;
}
.quantity__button--decrease {
  background: url("../img/minus.svg") center/16px no-repeat;
}
.quantity__button--increase {
  background: url("../img/plus.svg") center/16px no-repeat;
}
.no-js .quantity__button {
  display: none;
}
.quantity__button:hover, .quantity__button:focus {
  border-color: var(--color-gray-light);
}
.quantity__button:focus {
  outline: none;
}

.quantity__input {
  width: 2ch;
  padding: 0;
  font-size: 24px;
  line-height: 1;
  text-align: center;
  color: var(--color-gray);
  background: none;
  border: none;
  appearance: none;
}
.quantity__input:focus {
  outline: none;
}

.similar {
  margin-bottom: 52px;
}
@media only screen and (min-width: 720px) {
  .similar {
    margin-bottom: 48px;
  }
}
@media only screen and (min-width: 1360px) {
  .similar {
    margin-bottom: 64px;
  }
}

.similar__title {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  color: var(--color-gray);
  text-transform: uppercase;
  margin-bottom: 8px;
}

.similar__list {
  display: grid;
  justify-content: center;
  gap: 16px;
}
@media only screen and (min-width: 720px) {
  .similar__list {
    gap: 28px;
  }
}
@media only screen and (min-width: 1360px) {
  .similar__list {
    grid-template-columns: repeat(2, 296px);
    gap: 24px;
  }
}

.collection {
  position: relative;
  box-sizing: border-box;
  width: 288px;
  height: 160px;
  padding: 12px 16px;
  border-radius: 4px;
}
@media only screen and (min-width: 720px) {
  .collection {
    width: 688px;
    height: 287px;
    padding-top: 18px;
    padding-bottom: 18px;
  }
}
@media only screen and (min-width: 1360px) {
  .collection {
    width: 296px;
    height: 160px;
    padding-top: 12px;
    padding-bottom: 12px;
  }
}

.collection__link {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
}
.collection__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: linear-gradient(var(--color-white), #352925);
  mix-blend-mode: multiply;
  border-radius: 4px;
  opacity: 0.2;
}

.collection__image {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 4px;
}

.collection__content {
  position: relative;
  z-index: 1;
  color: var(--color-white);
}

.collection__title {
  margin: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 1;
}
@media only screen and (min-width: 720px) {
  .collection__title {
    font-size: 40px;
  }
}
@media only screen and (min-width: 1360px) {
  .collection__title {
    font-size: 24px;
  }
}

.collection__text {
  margin: 0;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.5;
}
@media only screen and (min-width: 720px) {
  .collection__text {
    font-size: 18px;
    line-height: 18px;
  }
}
@media only screen and (min-width: 1360px) {
  .collection__text {
    font-size: 14px;
    line-height: 16px;
  }
}

.subscribe {
  margin-bottom: 16px;
}
@media only screen and (min-width: 720px) {
  .subscribe {
    margin-bottom: 29px;
  }
}

.subscribe__heading {
  margin-bottom: 28px;
}
@media only screen and (min-width: 720px) {
  .subscribe__heading {
    margin-bottom: 24px;
  }
}

.subscribe__title {
  margin: 0;
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
}
@media only screen and (min-width: 720px) {
  .subscribe__title {
    margin-bottom: 0;
    font-size: 32px;
    line-height: 1.25;
  }
}

.subscribe__text {
  margin: 0;
  font-style: 18px;
  line-height: 1.56;
  color: var(--color-gray);
  opacity: 0.8;
}
@media only screen and (min-width: 720px) {
  .subscribe__text {
    font-size: 20px;
    line-height: 1.6;
  }
}

.form__wrapper {
  max-width: 456px;
}

.form__field {
  margin-bottom: 16px;
}

.form__input input {
  box-sizing: border-box;
  width: 100%;
  padding: 0 16px;
  font: inherit;
  line-height: 2.5;
  color: inherit;
  background-color: var(--color-gray-transparent);
  border: none;
  border-radius: 1px;
  appearance: none;
}
.form__input input::placeholder {
  color: var(--color-gray);
  opacity: 1;
}

.form__gender {
  margin-top: -4px;
  margin-bottom: 32px;
}
@media only screen and (min-width: 720px) {
  .form__gender {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
  }
}

.form__text {
  margin: 0;
  margin-bottom: 4px;
  font-size: 20px;
  line-height: 1.6;
  color: var(--color-gray);
  opacity: 0.8;
}
@media only screen and (min-width: 720px) {
  .form__text {
    margin-right: 24px;
    margin-bottom: 0;
  }
}

.form__radios {
  display: flex;
}

.form__radio {
  position: relative;
}
.form__radio:not(:last-child) {
  margin-right: 24px;
}
.form__radio input {
  position: absolute;
  top: 2px;
  left: 0;
  z-index: 1;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  appearance: none;
}
.form__radio input:checked + label::before {
  background-image: radial-gradient(var(--color-primary) 50%, transparent 50%);
}
.form__radio input:hover + label::before, .form__radio input:focus + label::before {
  border-color: var(--color-gray);
}
.form__radio label {
  display: block;
  min-height: 24px;
  padding-left: 28px;
  color: var(--color-primary-transparent);
  cursor: pointer;
  user-select: none;
}
.form__radio label::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  box-sizing: border-box;
  width: 20px;
  height: 20px;
  border: 1px solid var(--color-gray-light);
  border-radius: 50%;
  box-shadow: inset 0 0 0 3px var(--color-white);
  transition: border-color 0.4s;
}

.form__checkbox {
  position: relative;
}
.form__checkbox input {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  box-sizing: border-box;
  width: 20px;
  height: 20px;
  border-radius: 1px;
  cursor: pointer;
  opacity: 0;
  appearance: none;
}
.form__checkbox input:hover + label::before, .form__checkbox input:focus + label::before {
  border-color: var(--color-primary);
}
.form__checkbox input:checked + label::before {
  background-color: var(--color-primary);
  border-color: var(--color-primary);
}
.form__checkbox input:checked + label::after {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  width: 10px;
  height: 10px;
  background: url("../img/check.svg") center/10px no-repeat;
  filter: brightness(0) invert(1);
}
.form__checkbox input:checked:hover + label::before, .form__checkbox input:checked:focus + label::before {
  background-color: transparent;
}
.form__checkbox input:checked:hover + label::after, .form__checkbox input:checked:focus + label::after {
  filter: none;
}
.form__checkbox label {
  display: block;
  padding-left: 28px;
  color: var(--color-primary-transparent);
  cursor: pointer;
  user-select: none;
}
.form__checkbox label::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 20px;
  height: 20px;
  border: 1px solid var(--color-gray-light);
  border-radius: 1px;
  transition: background-color 0.4s, border-color 0.4s;
}

.main__wrapper {
  max-width: 600px;
  margin: 0 auto;
}
@media only screen and (min-width: 720px) {
  .main__wrapper {
    max-width: 1000px;
  }
}
@media only screen and (min-width: 1360px) {
  .main__wrapper {
    position: relative;
    display: grid;
    grid-template-columns: 600px 720px;
    justify-content: space-between;
    align-items: start;
    max-width: 1360px;
  }
}

@media only screen and (min-width: 1360px) {
  .main__slider {
    position: sticky;
    top: 0;
  }
}